export const randomEmojiFromMart = () => {
  const emojis = [
    'smiley',
    'rolling_on_the_floor_laughing',
    'joy',
    'wink',
    'smirk',
    'stuck_out_tongue',
    'stuck_out_tongue_winking_eye',
    'zany_face',
    'smile',
    'sweat_smile',
    'laughing',
    'innocent',
    'blush',
    'grin',
    'upside_down_face',
  ];
  return emojis[Math.floor(Math.random() * emojis.length)];
};

